@import "./src/commom/styles/sass/variables";
@import "./src/commom/styles/sass/mixins";

.title {
  @include flex(row,space-between, center, nowrap );
  width: 100%;
  margin: 0 0 50px;
  text-transform:  capitalize;
  line-height: 1.2;
  position: relative;
  padding: 0 10px;

  & i {
    font-size: 36px;
    color: $primaryColor;
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -20px;
    border-bottom: 1px dashed rgba(255, 255, 255, 0.2);
    width: 100%;
  }

  & h2 {
    @include text($primaryColor, $fontFamilyTitle, 700, 36px, 1em);
    letter-spacing: 1.5px;
  }
}

